import React from 'react'
import { FaFacebook, FaInstagram, FaYoutube, FaTwitter, FaLinkedin } from 'react-icons/fa'
import {
    FooterContainer,
    FooterWrap,
    FooterLinksContainer,
    FooterWrapper,
    FooterLinkItems,
    FooterLinkTitle,
    FooterLink,
    SocialMedia,
    SocialIcons,
    SocialIconLink
} from './FooterElements'


const Footer = () => {
    return (
        <FooterContainer>
            <FooterWrap>
                <FooterLinksContainer>
                    <FooterWrapper>
                        <FooterLinkItems>
                            <img style={{ width: '135px', height: '45px', marginBottom: '20px' }} src="https://logos-download.com/wp-content/uploads/2016/09/React_logo_wordmark.png" />
                            <FooterLink style={{color: '#e0752f'}} to='/'>{new Date().getFullYear()} &copy; All rights reserved.</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLink to='/'>About</FooterLink>
                            <FooterLink to='/'>Services</FooterLink>
                            <FooterLink to='/'>Products</FooterLink>
                        </FooterLinkItems>
                    </FooterWrapper>
                    <FooterWrapper>
                        <FooterLinkItems>
                            <FooterLink to='/'>Details</FooterLink>
                            <FooterLink to='/'>Contact</FooterLink>

                        </FooterLinkItems>
                    </FooterWrapper>

                    <SocialMedia>
                        <FooterLinkTitle to='/'>Follow us</FooterLinkTitle>
                        <SocialIcons>
                            <SocialIconLink href="/">
                                <FaInstagram />
                            </SocialIconLink>
                            <SocialIconLink href="/">
                                <FaFacebook />
                            </SocialIconLink>
                            <SocialIconLink href="/">
                                <FaTwitter />
                            </SocialIconLink>
                            <SocialIconLink href="/">
                                <FaYoutube />
                            </SocialIconLink>
                            <SocialIconLink href="/">
                                <FaLinkedin />
                            </SocialIconLink>
                        </SocialIcons>
                    </SocialMedia>
                </FooterLinksContainer>

            </FooterWrap>
        </FooterContainer>
    )
}

export default Footer