import styled from "styled-components";

export const Carou = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 15px;
  width: 80%;
  margin-right: auto;
    margin-left: auto;
`;

export const Item = styled.div`
      background-color: white;
        height: 408px;
        width: 276px;
        box-shadow: 0px 8px 24px #42533B66;
        opacity: 1;
        border-bottom: solid #e0852f;
        border-width: 10px;
`;

export const CarouselText = styled.div`
      color: #343434;
      margin-right: auto;
    margin-left: auto;
    justify-content: center;
    align-items: center;
    display: grid;
    margin-top: 20px;

`;

export const CarouContent = styled.div`
  background-color: #dce1da;
  color: #343434;
  font-size: 20px;
  margin-bottom: 50px;
  height: 752px;
    width: 100%;
`;

export const Header = styled.h1`
    margin-right: auto;
    margin-left: auto;
    padding-top: 120px;
    font-size: 21px;
    margin-bottom: 20px;
    justify-content: center;
    align-items: center;
    line-height: 1.1;
    font-weight: 600;
    color: #e0752f;

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`

export const HeaderTwo = styled.h1`
    margin-right: auto;
    margin-left: auto;
    color: #343434;
    padding-top: 50px;
    font-size: 21px;
    margin-bottom: 40px;
    justify-content: center;
    align-items: center;
    line-height: 1.1;
    font-weight: 600;
    max-width: 200px;

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`

export const Subtitle = styled.h3`
    max-width: 700px;
    width: 100%;
    font-size: 39px;
    line-height: 24px;
    color: #343434;
    margin-bottom: 50px;
    margin-left: 20px;
    margin-right: 20px;
    @media screen and (max-width: 768px){
        font-size: 25px;
    }
`

export const SubtitleTwo = styled.p`
    max-width: 270px;
    font-size: 20px;
    color: #343434;
    margin-bottom: 40px;
    padding-left: 12px;
    @media screen and (max-width: 480px){
        font-size: 18px;
        margin-bottom: 20px;
    }
`

