import styled from 'styled-components'


export const MoreDetailsContainer = styled.div`
    height: 100%;
    background-color: #f2f4f1;
`

export const InfoWrapper = styled.div`
    display: grid;
    z-index: 1;
    width: 100%;
    max-width: 1100px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 24px;
    justify-content: center;
    align-items: center;
`

export const Heading = styled.h1`
    margin-right: auto;
    margin-left: auto;
    color: #343434;
    margin-bottom: 24px;
    margin-top: 130px;
    font-size: 39px;
    justify-content: center;
    align-items: center;
    line-height: 1.1;

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`

export const Subtitle = styled.p`
    max-width: 850px;
    margin-bottom: 65px;
    font-size: 18px;
    justify-content: center;
    align-items: center;
    color: #343434;
    margin-right: auto;
    margin-left: auto;
`

export const Subtitle2 = styled.p`
margin-right: auto;
    margin-left: auto;
    max-width: 206px;
    margin-bottom: 25px;
    font-size: 21px;
    justify-content: center;
    align-items: center;
    color: #343434;
    font-weight: bold;
`

export const SvgIcon = styled.svg`

`
export const InfoRow = styled.div`
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    grid-template-areas: ${({ imgStart }) => (imgStart ? `'col4 col3 col2 col1'` : `'col1 col2 col3 col4'`)};
    margin-right: auto;
    margin-left: auto;
    padding: 0 50px;
    max-width: 1400px;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 768px){
        grid-template-areas: ${({ imgStart }) => (imgStart ? `'col1' 'col2' 'col3' 'col4'` : `'col1 col1' 'col2 col2' 'col3 col3' 'col4 col4'`)};
    }
`

export const Column1 = styled.div`
    margin-bottom: 15px;
    padding:0 15px;
    grid-area: col1;
`

export const Column2 = styled.div`
    margin-bottom: 15px;
    padding:0 15px;
    grid-area: col2;
`

export const Column3 = styled.div`
    margin-bottom: 15px;
    padding:0 15px;
    grid-area: col3;
`

export const Column4 = styled.div`
    margin-bottom: 15px;
    padding:0 15px;
    grid-area: col4;
`

export const Wrapper = styled.div`
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 60px;
`