import styled from 'styled-components'

export const HeroContainer = styled.div`
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 0 60px;
    height: 768px;
    position: relative;
    z-index: 1;
    width: 100%;

    @media screen and (max-width: 768px){
        justify-content: center;
        padding: 0 30px;
    }
`


export const HeroBg = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left:0;
    height: 100%;
    width: 100%;

    overflow: hidden;
`

export const HeroContent = styled.div`
    z-index: 3;
    max-width: 1000px;
    position: absolute;
    padding: 8px 80px;
    display: flex;
    flex-direction: column;
    align-items: start;

    @media screen and (max-width: 768px){
        align-items: center;
    }
`

export const HeroH1 = styled.h1`
    color:#fff;
    font-size: 48px;
    text-align: start;
    max-width: 570px;
    margin-bottom: 20px;
    @media screen and (max-width: 768px){
        font-size: 40px;
        text-align: center;

    }

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`

export const HeroP = styled.p`
    margin-top: 24px;
    color:#fff;
    font-size: 16px;
    text-align: start;
    max-width: 372px;

    @media screen and (max-width: 768px){
        font-size: 14px;
        text-align: center;

    }

    @media screen and (max-width: 480px){
        font-size: 18px;
    }
`

export const HeroBtnWrapper = styled.div`
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: start;

    @media screen and (max-width: 768px){
        align-items: center;

    }
`

export const ImgContent = styled.img`
object-fit: cover;
-o-object-fit: cover;
`