import styled from 'styled-components'

export const InfoContainer = styled.div`
    height: 768px;
    margin-right: auto;
    margin-left: auto;
    color: #fff;
    background: #f2f4f1;
    @media screen and (max-width: 768px){
        padding: 100px 0;
    }
`

export const InfoWrapper = styled.div`
display: grid;
z-index: 1;
width: 100%;
max-width: 1920px;
margin-right: auto;
margin-left: auto;
align-items: center;
justify-content: center;
padding: 0 24px;
justify-content: center;
`

export const InfoRow = styled.div`

display: grid;
grid-auto-columns: minmax(auto, 1fr);
align-items: center;
grid-template-areas: ${({ imgStart }) => (imgStart ? `'col2 col1'` : `'col1 col2'`)};


`

export const ImgW = styled.img`

@media screen and (max-width: 1200px){
    display: none;
}
`



export const Column1 = styled.div`
    margin-bottom: 15px;
    padding:0 15px;
    grid-area: col1;
`

export const Column2 = styled.div`
    margin-bottom: 15px;
    padding:0 15px;
    grid-area: col2;
`


export const TextWrapper = styled.div`
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 60px;
`

export const TopLine = styled.p`
    color: #01bf71;
    font-size: 16px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    margin-bottom: 16px;
`

export const Heading = styled.h1`
    color: #000;
    margin-bottom: 8px;
    margin-top: 150px;
    font-size: 39px;
    line-height: 1.1;
    font-weight: 600;
    color: ${({ lightText }) => (lightText ? '#f7f8fa' : '010606')};

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`

export const Heading2 = styled.h1`
    color: #e0752f;
    margin-bottom: 35px;
    font-size: 39px;
    line-height: 1.1;
    font-weight: 600;
    color: ${({ lightText }) => (lightText ? '#f7f8fa' : '010606')};

    @media screen and (max-width: 480px){
        font-size: 32px;
    }
`

export const Subtitle = styled.p`
    max-width: 440px;
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 24px;
    color: ${({ darkText }) => (darkText ? '#fff' : '#010606')};
`

export const BtnWrap = styled.div`
    display: flex;
    justify-content: flex-start;
    margin-top: 50px;
`

export const ImgWrap = styled.div`
    max-width: 555px;
    height: 100%;
`

export const Img = styled.img`
    width: 100%;
    margin: 0 0 10px 0;
    padding-right: 0;
`

export const FormContainer = styled.form`
    margin-bottom: 16px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding: 0 24px;
    justify-content: center;
`

export const InputContent = styled.div`
    margin-top: 20px;
    margin-bottom: 10px;
    height: 50px;
`
export const InputText = styled.input`
        background-color: transparent;
        font-size: 16px;
        font-weight: 700;
        color: #5f6368;
        letter-spacing: 1px;
        border: 0;
        border-bottom: solid gray;
        border-width: 1px;
        width: 471px;
        outline: none;
        @media screen and (max-width: 768px){
            width: auto;

}
        &:hover{
        transition: all 0.2s ease-in-out;
        color: #e0752f;
        border-color: #e0752f;
        ::placeholder,
        ::-webkit-input-placeholder {
            color: #e0752f;
        }
        :-ms-input-placeholder {
            color: #e0752f;
        }
    };

`
export const SpanText = styled.span`
    color: red;
    font-weight: bold;
    padding: 2px;
    font-style: italic;
`
